<template>
  <div class="container">
    <loading :active.sync="isLoading"></loading>
    <div class="row my-3">
      <div class="col-md-6">
        <Page @postPage="getCoupons" :propsPage="pagination" />
      </div>
      <div class="col-md-6 text-right">
        <button class="btn btn-primary" @click="openModal('new')"></button>
      </div>
    </div>

    <table class="table table-hover">
      <thead>
        <th></th>
        <th width="130"></th>
        <th width="130"></th>
        <th width="130"></th>
        <th width="130"></th>
      </thead>
      <tbody>
        <tr v-for="item in coupons" :key="item.id">
          <td>{{ item.title }}</td>
          <td>{{ item.percent }}%</td>
          <td>{{ item.due_date | date }}</td>
          <td>
            <span class="text-success" v-if="item.is_enabled"></span>
            <span class="text-secondary" v-else></span>
          </td>
          <td>
            <div class="btn-group" role="group" aria-label="Basic example">
              <button
                type="button"
                class="btn btn-outline-primary btn-sm"
                @click="openModal('edit', item)"
              ></button>
              <button
                type="button"
                class="btn btn-outline-danger btn-sm"
                @click="openModal('delete', item)"
              ></button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>

    <!-- Modal  -->
    <div
      class="modal fade"
      id="couponModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel" v-if="modalType === 'new'"></h5>
            <h5 class="modal-title" id="exampleModalLabel" v-else></h5>
            <button type="button" class="close" aria-label="Close" data-dismiss="modal">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label for="title"></label>
              <input
                type="text"
                class="form-control"
                id="title"
                v-model="tempCoupon.title"
                placeholder=""
              />
            </div>
            <div class="form-group">
              <label for="coupon_code"></label>
              <input
                type="text"
                class="form-control"
                id="coupon_code"
                v-model="tempCoupon.code"
                placeholder=""
              />
            </div>
            <div class="form-group">
              <label for="due_date"></label>
              <input type="date" class="form-control" id="due_date" v-model="due_date" />
            </div>
            <div class="form-group">
              <label for="price"></label>
              <input
                type="number"
                class="form-control"
                id="price"
                v-model="tempCoupon.percent"
                placeholder=""
              />
            </div>
            <div class="form-group">
              <div class="form-check">
                <input
                  class="form-check-input"
                  type="checkbox"
                  :true-value="1"
                  :false-value="0"
                  v-model="tempCoupon.is_enabled"
                  id="is_enabled"
                />
                <label class="form-check-label" for="is_enabled"></label>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            <button type="button" class="btn btn-primary" @click="updateCoupon()"></button>
          </div>
        </div>
      </div>
    </div>

    <!-- Modal  -->
    <div
      class="modal fade"
      id="delModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
              @click="closeModal()"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{ tempCoupon.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-outline-secondary"
              data-dismiss="modal"
              @click="closeModal()"
            ></button>
            <button type="button" class="btn btn-danger" @click="updateCoupon()"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Page from '../Pagination'
import $ from 'jquery'

export default {
  components: {
    Page
  },

  data () {
    return {
      isLoading: false, // vue-loading-overlay 
      coupons: [], //  ajax 
      tempCoupon: {}, // 
      pagination: {}, //  ajax  page  props  Pagination.vue
      modalType: 'new', // modal 
      due_date: ''
    }
  },

  watch: {
    //  due_date  tempCoupon.due_date
    due_date () {
      const timestamp = Math.floor(new Date(this.due_date) / 1000)
      this.tempCoupon.due_date = timestamp
    }
  },

  methods: {
    //  ajax  this.coupons
    // (Pagination ) 
    getCoupons (page = 1) {
      this.isLoading = true
      const api = `${process.env.API_PATH}/api/${process.env.API_ADMIN}/admin/coupons?page=${page}`
      this.$http.get(api).then((response) => {
        console.log('', response.data)
        this.isLoading = false
        this.coupons = response.data.coupons
        this.pagination = response.data.pagination
      })
    },

    //  Modal ()
    openModal (modalType, item) {
      const vm = this
      if (modalType === 'new') {
        vm.modalType = 'new'
        vm.tempCoupon = {}
        $('#couponModal').modal('show')
      } else if (modalType === 'edit') {
        vm.modalType = 'edit'
        vm.tempCoupon = item
        $('#couponModal').modal('show')
      } else if (modalType === 'delete') {
        vm.modalType = 'delete'
        vm.tempCoupon = item
        $('#delModal').modal('show')
      }
    },

    // 
    updateCoupon () {
      const vm = this
      let api = ''
      let httpMethod = ''
      if (this.modalType === 'new') {
        api = `${process.env.API_PATH}/api/${process.env.API_ADMIN}/admin/coupon`
        httpMethod = 'post'
      } else if (this.modalType === 'edit') {
        api = `${process.env.API_PATH}/api/${process.env.API_ADMIN}/admin/coupon/${this.tempCoupon.id}`
        httpMethod = 'put'
      } else if (this.modalType === 'delete') {
        api = `${process.env.API_PATH}/api/${process.env.API_ADMIN}/admin/coupon/${this.tempCoupon.id}`
        httpMethod = 'delete'
      }

      vm.$http[httpMethod](api, {'data': vm.tempCoupon}).then((response) => {
        console.log(this.modalType, response.data)
        this.getCoupons()
        $('#couponModal').modal('hide')
        $('#delModal').modal('hide')
      })
    }
  },

  created () {
    this.getCoupons()
  }
}
</script>



