// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import 'bootstrap'
import axios from 'axios'
import vueAxios from 'vue-axios'
import VeeValidate from 'vee-validate'
import zhTW from 'vee-validate/dist/locale/zh_TW' // 
import Loading from 'vue-loading-overlay'
import 'vue-loading-overlay/dist/vue-loading.css' // vue-loading-overlay CSS 

import currency from './filters/currency' // $ filters.js
import date from './filters/date'
import './bus' // eventBus

Vue.use(vueAxios, axios)
Vue.use(VeeValidate)
VeeValidate.Validator.localize('zh-TW', zhTW) // 
Vue.component('Loading', Loading) //  vue-loading-overlay  Vue.use(Loading) 

Vue.filter('currency', currency) //  filters.js
Vue.filter('date', date)

Vue.config.productionTip = false
axios.defaults.withCredentials = true

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})

//  (: )
router.beforeEach((to, from, next) => {
  console.log('to', to, 'from', from, 'next', next)
  if (to.meta.requiresAuth) {
    //  /api/user/check
    const api = `${process.env.API_PATH}/api/user/check`
    axios.post(api).then((response) => {
      console.log('', response.data)
      if (response.data.success) {
        next()
      } else {
        next('/login') //  login 
      }
    })
  } else {
    next()
  }
})



