<template>
  <div class="message-alert">
    <div
      class="alert alert-dismissible"
      :class="'alert-' + item.status"
      v-for="(item, i) in messages"
      :key="i"
    >
      {{ item.message }}
      <button
        type="button"
        class="close"
        @click="removeMessage(i)"
        aria-label="Close"
      >
        <span aria-hidden="true">&times;</span>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      messages: [] // {}
    }
  },
  methods: {
    //  alert 5 
    // : 
    updateMessage (message, status) {
      const timestamp = Math.floor(new Date() / 1000) //  id
      this.messages.push({
        message, // 
        status, // bootstrap  (: alert-primarystatus = primarystatus)
        timestamp //  timestamp  id
      })
      this.removeMessageWithTiming(timestamp) //  alert
    },
    //  alert  x 
    removeMessage (num) {
      this.messages.splice(num, 1)
    },
    //  5 
    removeMessageWithTiming (timestamp) {
      const vm = this
      setTimeout(() => { // setTimeout('/function', '')
        //  timestamp
        vm.messages.filter((item, index) => {
          if (item.timestamp === timestamp) {
            return vm.messages.splice(index, 1)
          }
        })
      }, 5000)
      // setTimeout(() => {
      //   vm.messages.forEach((item, i) => {
      //     if (item.timestamp === timestamp) {
      //       vm.messages.splice(i, 1)
      //     }
      //   })
      // }, 5000)
    }
  },
  // 
  created () {
    const vm = this
    // $on  ( JS  addEventListener)
    //  'messsage:push'
    // message: 
    // status:  warning
    vm.$bus.$on('messsage:push', (message, status = 'warning') => {
      vm.updateMessage(message, status)
    })
  }
}
</script>

<style scope>
.message-alert {
  position: fixed;
  max-width: 50%;
  top: 56px;
  right: 20px;
  z-index: 1100;
}
</style>



